.navigation {
  background-color: #81b3d2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navigation__link {
  border-bottom: 1px solid #678fa8;
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  width: 100%;
  text-align: center;
}
.header {
  display: flex;
  flex-direction: column-reverse;
}
.header__logo {
  display: block;
  min-height: 57px;
}
@font-face {
  font-family: "PT Sans";
  src: url("/source/fonts/ptsans.woff2") format("woff2"), url("/source/fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: url("/source/fonts/ptsansbold.woff2") format("woff2"), url("/source/fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
body {
  font-family: "PT Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.common--title {
  font-weight: bold;
  text-align: center;
  line-height: 26px;
  font-size: 18px;
}
img {
  display: block;
}
.common--about-info {
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}
.common--mountain-background {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  width: auto;
  min-height: 400px;
  background-size: cover;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.welcome {
  line-height: 26px;
}
.welcome__description {
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}
.welcome__description span {
  font-size: 14px;
}
.advantages__list {
  padding: 0;
  margin: 0;
}
.advantages__promo--info {
  background-color: #81b3d2;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  font-size: 14px;
  line-height: 21px;
  padding: 40px 28px;
  text-align: center;
}
.advantages__promo__title {
  font-weight: bold;
  font-size: 18px;
}
.advantages__list-item p:first-of-type {
  font-size: 18px;
  font-weight: bold;
}
.advantages__list-item {
  text-align: center;
  padding: 30px;
}
.list-item__info {
  font-size: 14px;
  line-height: 21px;
}
.bottom-list {
  background-color: #eeeeee;
}
.information__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: bold;
}
.information-details {
  font-size: 14px;
  line-height: 24px;
}
.main-style-btn {
  background-color: #81b3d2;
  color: #ffffff;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: bold;
  width: 100%;
  padding: 20px 20px;
  border-style: none;
}
.information__wrapper {
  text-align: center;
  padding: 0 20px 50px;
}
.galary {
  padding: 0 20px;
}
.welcome-galary__img {
  min-height: 161px;
}
.galary-greeting__wrapper {
  display: none;
  padding: 30px;
}
.galary__item {
  border-bottom: 1px solid #f2f2f2;
}
.galary__item-description {
  font-size: 14px;
  font-weight: bold;
  line-height: 24px;
}
.galary__item-author {
  text-transform: none;
}
.galary__item-likes {
  text-transform: none;
}
.form {
  padding: 0 20px;
}
.review__wrapper {
  padding: 0 20px;
}
.review__about {
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}
.form__introduction p {
  font-weight: bold;
}
.form label {
  text-transform: initial;
}
.form__cell {
  margin-bottom: 20px;
  font-size: 14px;
}
.form__review--checkbox {
  font-size: 18px;
}
.contacts__input {
  width: 100%;
}
.gray--input {
  padding: 20px;
  border-style: none;
  background-color: #f2f2f2;
}
.feedback__input {
  width: 100%;
  margin-bottom: 50px;
}
.feedback {
  margin-bottom: 74px;
}
footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer a {
  text-decoration: none;
  color: #000000;
}
.footer__main-link {
  font-size: 18px;
  font-weight: bold;
}
